<template>
  <div class="page-content">
    <topbar title=""
      subtitle=""/>
    <div class="template-container">
      <div class="template-form">
        <el-form ref="form" :model="form" :rules="rules" label-width="80px">
          <el-form-item prop="name" label="">
            <el-input v-model="form.name" placeholder=""></el-input>
          </el-form-item>
          <el-form-item label="">
            <el-button size="small"
              @click="onChooseFile">
              <i class="el-icon-upload el-icon--right"></i>
            </el-button>
            <span class="template-file-name">{{uploadFileName}}</span>
          </el-form-item>
          <el-form-item prop="thumbnail" label="">
            <el-button size="small"
              @click="onChooseImage">
              <i class="el-icon-picture el-icon--right"></i>
            </el-button>
            <image-frame
              :src="getThumbnailUrl(form.thumbnail)">
            </image-frame>
          </el-form-item>
          <el-form-item label="">
            <el-button type="primary" size="small" @click="onSubmit"></el-button>
            <el-button type="" size="small" @click="onBack"></el-button>
          </el-form-item>
        </el-form>
      </div>
    </div>
    <form id="fileForm" enctype="multipart/form-data">
      <input style="display:none;"
        ref="tplPageFileInput"
        type="file" name="file"
        @change="getFile"/>
      <input style="display:none;"
        ref="tplPageImageInput"
        type="file" name="file"
        @change="getImage"/>
    </form>
  </div>
</template>

<script>
import { APIS } from 'comp/util/constants';
import fetch from 'comp/util/fetch';
import Topbar from 'comp/topbar';
import ImageFrame from 'comp/image-frame';

const defaultImage = 'https://avatars3.githubusercontent.com/u/38666040';
export default {
  name: 'template-add',
  components: {
    topbar: Topbar,
    'image-frame': ImageFrame,
  },
  data() {
    return {
      form: {
        name: '',
        thumbnail: defaultImage,
      },
      rules: {
        name: [
          { required: true, message: '', trigger: 'blur' },
          { min: 3, max: 15, message: ' 3  16 ', trigger: 'blur' }
        ],
      },
      templateId: '',
      uploadFileName: '',
      uploadImageName: '',
      template: null,
    };
  },
  async mounted() {
    this.templateId = this.$route.query.templateId;

    this.template = await this.getTemplate(this.templateId);

    this.form.name = this.template.name;
    this.form.thumbnail = this.template.thumbnail;
  },
  methods: {
    async getTemplate(templateId) {
      const ret = await fetch(`${APIS.TEMPLATE}/${templateId}`, {
      });
      return ret;
    },
    getFile(event) {
      const file = event.target.files[0];
      const fileName = file.name;

      if (!/zip$/.test(fileName)) {
        this.$refs.tplPageFileInput.value = null;
        return this.$message({
          message: '(.zip).',
          type: 'warning',
        });
      }

      //  10M 
      if (file.size > 1024 * 1024 * 10) {
        return this.$message({
          message: ' 10M .',
          type: 'warning',
        });
      }

      const formData = new FormData();
      formData.append('templateId', this.templateId);
      formData.append('file', file);

      fetch(`${APIS.FILE}/upload`, {
        method: 'POST',
        headers: {
          'Content-Type': 'multipart/form-data',
        },
        body: formData,
      }).then(() => {
        this.uploadFileName = fileName;
        this.$refs.tplPageFileInput.value = null;
        this.$message({
          message: '.',
          type: 'success',
        });
      }).catch(() => {
        this.$message({
          message: ', .',
          type: 'warning',
        });
        this.$refs.tplPageFileInput.value = null;
      });
    },
    getImage() {
      const file = event.target.files[0];
      const fileName = file.name;

      if (!/bmg|gif|jpg|jpeg|pic|png$/i.test(fileName)) {
        this.$refs.tplPageImageInput.value = null;
        return this.$message({
          message: '.',
          type: 'warning',
        });
      }

      //  2M 
      if (file.size > 1024 * 1024 * 2) {
        return this.$message({
          message: ' 2M .',
          type: 'warning',
        });
      }

      const formData = new FormData();
      formData.append('templateId', this.templateId);
      formData.append('file', file);

      this.form.thumbnail = URL.createObjectURL(file);
      fetch(`${APIS.FILE}/upload`, {
        method: 'POST',
        headers: {
          'Content-Type': 'multipart/form-data',
        },
        body: formData,
      }).then(() => {
        this.uploadImageName = fileName;
        this.$refs.tplPageImageInput.value = null;
        this.$message({
          message: '.',
          type: 'success',
        });
      }).catch(() => {
        this.form.thumbnail = defaultImage;
        this.$message({
          message: ', .',
          type: 'warning',
        });
        this.$refs.tplPageImageInput.value = null;
      });
    },
    async onSubmit() {
      const validate = await this.$refs.form.validate().catch(e => e);

      if (!validate) {
        this.$message({
          message: '.',
          type: 'warning',
        });
        return;
      }

      // , 
      if (validate) {
        const body = {};
        if (this.form.name) {
          body.name = this.form.name;
        }
        if (this.uploadFileName) {
          body.fileName = this.uploadFileName;
        }
        if (this.uploadImageName) {
          body.imageName = this.uploadImageName;
        }

        fetch(`${APIS.TEMPLATE}/${this.templateId}`, {
          method: 'PUT',
          body,
        }).then(() => {
          this.$message({
            message: '.',
            type: 'success',
          });
          this.clearFormData();
        }).catch(() => {
          this.$message({
            message: ', .',
            type: 'warning',
          });
        });
      }
    },
    getThumbnailUrl(url) {
      if (/^https?:\/\//.test(url)) {
        return url;
      }
      if (/^blob?:/.test(url)) {
        return url;
      }
      return `http://res.pipeline/${url}`;
    },
    onBack() {
      this.$router.replace({
        path: '/template/list',
      });
    },
    onChooseFile() {
      this.$refs.tplPageFileInput.click();
    },
    onChooseImage() {
      this.$refs.tplPageImageInput.click();
    },
    clearFormData() {
      this.uploadFileName = '';
      this.uploadImageName = '';
    },
  },
};
</script>

<style lang="less" scoped>
.page-content {
  height: 100%;
}

.template-container {
  display: flex;
  justify-content: center;
  padding-top: 50px;
}

.template-form {
  width: 600px;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: center;
  margin: 30px 50px;
}

.template-file-name {
  padding: 0 0 0 10px;
}
</style>



